.login-box {
    background: white;
    padding: 40px;
    border-radius: 10px;
    box-shadow: 0 15px 35px rgba(6, 47, 99, 0.3);
    width: 100%;
    max-width:400px;
}

.logo-container {
    text-align: center;
    margin-bottom: 20px;
}

.logo {
    width: 150px;
    height: auto;
    margin-bottom: 10px;
}

.login-box h2 {
    text-align: center;
    color:  #062f63;
    margin-bottom:30px;
    font-size:2em;
}

.form-group {
    position: relative;
    margin-bottom: 30px;
}

.form-group input {
    width: 100%;
    padding: 10px 0;
    font-size: 16px;
    border: none;
    border-bottom: 2px solid  #e0e0e0;
    outline: none;
    transition: 0.3s;
    background: transparent;
}

.form-group label {
    position: absolute;
    top: 0;
    left: 0;
    padding: 10px 0;
    font-size: 16px;
    color:  #062f63;
    pointer-events: none;
    transition: 0.3s ease;
}

.form-group input:focus,
.form-group input:valid {
    border-bottom-color:  #062f63;
}

.form-group input:focus ~ label,
.form-group input:valid ~ label {
    top: -20px;
    font-size: 12px;
    color:  #062f63;
}

.login-btn {
    width: 100%;
    padding: 12px;
    background:  #062f63;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: 0.3s;
}

.login-btn:hover {
    background: #0a4795;
    box-shadow: 0 5px 15px rgba(6, 47, 99, 0.3);
} 